<template>
    <div class="yuyue">
      <form @submit.prevent="submitOrder" id="orderForm">
        <div class="yu_content">
          <li>
            <div class="name label">姓名</div>
            <div class="ipt"><input type="text" id="name" name="nickname" v-model="orderInfo.nickname" placeholder="请输入您的姓名"></div>
          </li>
          <li>
            <div class="sex label">性别</div>
            <div class="ipt">
              <div class="ip_sex">
                <input  type="radio" value="0"  v-model="orderInfo.sex" id="female" name="sex">
                <label  for="female">女</label>
              </div>
              <div class="ip_sex">
                <input id="male" checked value="1"  v-model="orderInfo.sex"  type="radio" name="sex">
                <label for="male">男</label>
              </div>
            </div>
          </li>
          <li>
            <div class="birthday label">生日</div><div class="ipt brith"><div class="brithday" @click="openPicker()">{{orderInfo.birthday||"请选择"}} <span v-if="orderInfo.birthday" class="iconfont icon-yousanjiao"></span></div></div>
          </li>
          <li>
            <div class="mobile label">电话</div><div class="ipt"><input name="cellphone" v-model="orderInfo.cellphone" type="tel" id="mobile" placeholder="请输入您的电话"></div>
          </li>
          <li>
            <div class="bao label">社保</div>
            <div class="ipt">
              <div class="ip_sex">
                <input type="radio" v-model="orderInfo.is_bao"  value="0" @click="()=>{this.$set(this.orderInfo,'is_bao','0')}" id="not" name="is_bao">
                <label for="not">无</label>
              </div>
              <div class="ip_sex">
                <input type="radio" id="has"  v-model="orderInfo.is_bao" value="1"  @click="()=>{this.$set(this.orderInfo,'is_bao','1')}" checked name="is_bao">
                <label for="has">有</label>
              </div>
            </div>
          </li>
        </div>
        <van-button native-type="submit" type="default btn_yue" >预约</van-button>
      </form>

      <a href="" class="kefu iconfont icon-kefu"></a>
      <mt-datetime-picker
        ref="picker"
        type="date"
        year-format="{value} 年"
        month-format="{value} 月"
        date-format="{value} 日"
        :startDate="new Date('1940/1/1')"
        :endDate="new Date()"
        @confirm="handleConfirm">
      </mt-datetime-picker>

    </div>
</template>

<script>
  import {Toast } from 'vant'
  import { regMobile} from "../common/js/Reg";
import { apiPost } from '../common/js/api';
import { formatDate } from "../assets/utils"
  export default {
        name: "yuyue",
      data () {
        let query = this.$route.query;
          return {
            query:query,
            currentDate: new Date(),
            time:'请选择',
            pickerValue: '',
            sex:'male',
            namereg:'',
            has:"has",
            mobile:'',
            mobilereg:'',
            isJiao: true,
            orderInfo:{
              sex:'1',
              socialSecurity:'0',
              birthday:"",
              is_bao:1
            }
          }
      },
      methods: {
        openPicker() {
          this.$refs.picker.open();
        },
        handleConfirm (date) {
          date = formatDate(date,"yyyy-MM-dd")
          this.orderInfo.birthday = date;
        },
        coh(has){
          this.has=has;
          console.log(this.has)
        },

        submitOrder(){
          const { cellphone, birthday,nickname } = this.orderInfo;
          if(!nickname){
            Toast('请输入姓名')
            return
          }
          if(!birthday){
            Toast('请选择生日')
            return
          }
          if(!cellphone){
            Toast('请输入手机号码')
            return
          }
          if( cellphone.length !== 11 || !/^((13|14|15|17|18|10|19)[0-9]{1}\d{8})$/.test(Number(cellphone))){
            Toast('手机号格式有误')
            return
          }
          let form = document.getElementById("orderForm")
          let formData = new FormData(form)
          formData.append("birthday",birthday)

          formData.append("accessToken",this.$cookie.get('accessToken'))
          formData.append("insu_id",this.query.id)
          apiPost('/insurance/inserve',formData).then(res=>{
            Toast("预约成功")
            this.$router.push({
              name:'yyXiangqin',
              params:{
                id:res.data.data.id,
                mark:res.data.data.mark
              }
            })
          })
          

        }
      }
    }
</script>

<style scoped lang="scss">
  @import "./../common/css/common.css";
  .yuyue{
    background: #E6E9ED;
    width: 100%;
    height: 100%;
    font-size: 0.17rem;
    padding-top: 0.18rem;
    .yu_content{
      background: white;
      width: 3.5rem;
      margin:  0 auto;
      height: 2.54rem;
      border-radius: 0.04rem;
      li{
        height: 0.5rem;
        width: 100%;
        border: 1px solid #f5f5f5;
        line-height: 0.5rem;
        display: flex;
        div.label{
          width: .6rem;
          height:.5rem;
          text-align: center;
          font-size:.17rem;
          font-weight:400;
          color:rgba(51,51,51,1);
        }
        .ipt{
          flex: 1;
          input[type="text"],input[type='tel']{width: 97%;height: 0.3rem;border: none;text-align: right;margin-right: .1rem}
          .ip_sex{
            width: 0.7rem;
            float: right;
            color: #9a9a9a;
            display: flex;
            font-size: 0.17rem;
            justify-content: space-around;
            input{height: 0.5rem;width: 0.16rem}
          }
        }
        .brith{
          color: #9a9a9a;
          .brithday{
            width: 97%;
            text-align: right;
            color: #9a9a9a;
          }
          .brithdata{
            color: #333333;
          }
        }
      }
    }
    .date_bottom{
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
    }
    .btn_yue{
      width: 100%;
      height:0.47rem;
      font-size: 0.19rem;
      color: white;position: fixed;
      bottom: 0;
      background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
    }
    .kefu{
      width: 0.52rem;
      height: 0.52rem;
      background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
      border-radius:50%;
      box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
      position: fixed;
      bottom: 1.5rem;
      display: block;
      text-align: center;
      line-height: 0.52rem;
      font-size: 0.28rem;
      color: white;
      right: 0.11rem;
    }
  }
</style>



